import React from "react";
import styled from "styled-components";
import axios from "axios";
import { Link } from "react-router-dom";

import { Formik, Form, Field } from "formik";
import { Button } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
// import Grid from "@material-ui/core/Grid";
import { withStyles } from "@material-ui/core/styles";

import CustomizedInputPassword from "../components/inputPassword/inputPassword";

export function Login(props) {
  const { history } = props;
  const username = localStorage.getItem("username");
  if (username) {
    history.push("/blog-platform");
  }

  const initialValues = {
    name: "",
    password: "",
    email: "",
  };

  const StyledButton = withStyles({
    root: {
      height: "28px",
      alignSelf: "center",
      backgroundColor: "#1a73e8",
      color: "white",
      marginLeft: "0",
    },
  })(Button);

  const handleSubmit = async (values) => {
    const reqData = {
      user: {
        email: values.email,
        password: values.password,
      },
    };

    const response = await axios.post(
      "https://conduit.productionready.io/api/users/login",
      reqData
    );
    await localStorage.setItem("username", response.data.user.username);
    history.push("/blog-platform");
  };

  return (
    <Formik initialValues={initialValues} onSubmit={handleSubmit}>
      {({
        values,
        handleChange,
        handleBlur,
        errors,
        touched,
        dirty,
        isValid,
      }) => {
        const inputEmailProps = {
          size: "small",
          name: "email",
          as: "input",
          component: TextField,
          label: "Электронная почта",
          variant: "outlined",
          onChange: handleChange("email"),
          error: touched.email && Boolean(errors.email),
          onBlur: handleBlur("email"),
        };

        const passwordProps = {
          name: "password",
          errors,
          values,
          touched,
          onChange: handleChange("password"),
          onBlur: handleBlur("password"),
          label: "Пароль",
          labelWidth: 65,
          required: false,
        };

        return (
          <Form>
            <WrapperDiv>
              <HeadingDiv>
                <H1>Авторизация</H1>
              </HeadingDiv>

              <InputDiv>
                <Field {...inputEmailProps} />
                {touched.email ? errors.email : ""}
              </InputDiv>

              <InputDiv>
                <CustomizedInputPassword {...passwordProps} />
                {touched.password ? errors.password : ""}
              </InputDiv>

              <StyledButton
                variant="contained"
                size="small"
                color="primary"
                disabled={!dirty || !isValid}
                type="submit"
              >
                Войти
              </StyledButton>
            </WrapperDiv>
            <SignupLinkSpan>
              <Link to="/blog-platform/signup">Регистрация</Link>
            </SignupLinkSpan>
          </Form>
        );
      }}
    </Formik>
  );
}

const WrapperDiv = styled.div`
  display: flex;
  flex-direction: column;
`;

const HeadingDiv = styled.div`
  margin-bottom: 20px;
  padding-top: 0;
`;
const H1 = styled.h1`
  margin: 0;
`;

const InputDiv = styled.div`
  margin-bottom: 20px;
`;

const SignupLinkSpan = styled.span`
  display: block;
  padding-top: 20px;
`;

