import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import axios from "axios"

export function Home(props) {
  const username = localStorage.getItem("username");
  console.log("username", username);
  const { history } = props;
  if (!username) {
    history.push("/blog-platform/login");
  }
  const logout = () => localStorage.clear();

  const getListArticles = async () => {
    const response = await axios.get("https://conduit.productionready.io/api/articles");
    console.log("response.data: ", response.data);
    return await response.data.articles;
  }

  let listArticles

  getListArticles().then(response => response.map((item) => {
    return (
   <>
     <div>
       {item.title}
     </div>
     <div>
       {item.body}
     </div>
   </>
    )}));

console.log(listArticles);

  return (
    <>
      <HeadingDiv>
        <H1>Главная страница</H1>
      </HeadingDiv>
      Пользователь: {username}
      <div>
        <div>
        {/* {listArticles} */}
        </div>
        <Link to="/blog-platform/login" onClick={logout}>
          Выход
        </Link>
      </div>
    </>
  );
}

const HeadingDiv = styled.div`
  margin-bottom: 20px;
  padding-top: 0;
`;
const H1 = styled.h1`
  margin: 0;
`;
