import React from "react";
import styled from "styled-components";
import axios from "axios";

import { Link } from "react-router-dom";
import { Formik, Form, Field } from "formik";
import { Button } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
// import Grid from "@material-ui/core/Grid";
import { withStyles } from "@material-ui/core/styles";

import CustomizedInputPassword from "../components/inputPassword/inputPassword";

export function Signup(props) {
  const { history } = props;
  const username = localStorage.getItem("username");
  if (username) {
    history.push("/blog-platform");
  }

  const initialValues = {
    name: "",
    password: "",
    email: "",
  };

  const StyledButton = withStyles({
    root: {
      height: "28px",
      alignSelf: "center",
      backgroundColor: "#1a73e8",
      color: "white",
      marginLeft: "0",
    },
  })(Button);

  const handleSubmit = async (values) => {
    const reqData = {
      user: {
        username: values.name,
        email: values.email,
        password: values.password,
      },
    };

    await axios
      .post("https://conduit.productionready.io/api/users", reqData)
      .then((response) => {
        localStorage.setItem("username", response.data.user.username);
        history.push("/blog-platform");
      })
      .catch((error) => {
        console.log(error.response.data.errors);
      });
  };

  return (
    <Formik initialValues={initialValues} onSubmit={handleSubmit}>
      {({
        values,
        handleChange,
        handleBlur,
        errors,
        touched,
        dirty,
        isValid,
      }) => {
        const inputNameProps = {
          size: "small",
          name: "name",
          as: "input",
          component: TextField,
          label: "Имя",
          variant: "outlined",
          error: touched.name && Boolean(errors.name),
          value: values.name,
          onChange: handleChange("name"),
          onBlur: handleBlur("name"),
          required: true,
          width: 225,
        };

        const inputEmailProps = {
          size: "small",
          name: "email",
          as: "input",
          component: TextField,
          label: "Электронная почта",
          variant: "outlined",
          onChange: handleChange("email"),
          error: touched.email && Boolean(errors.email),
          onBlur: handleBlur("email"),
          required: true,
        };
        const passwordProps = {
          name: "password",
          errors,
          values,
          touched,
          onChange: handleChange("password"),
          onBlur: handleBlur("password"),
          label: "Пароль",
          labelWidth: 65,
          required: true,
        };

        return (
          <Form>
            <WrapperDiv>
              <HeadingDiv>
                <H1>Регистрация</H1>
              </HeadingDiv>

              <InputDiv>
                <Field {...inputNameProps} />
                {touched.name ? errors.name : ""}
              </InputDiv>

              <InputDiv>
                <Field {...inputEmailProps} />
                {touched.email ? errors.email : ""}
              </InputDiv>

              <InputDiv>
                <CustomizedInputPassword {...passwordProps} />
                {touched.password ? errors.password : ""}
              </InputDiv>

              <StyledButton
                variant="contained"
                size="small"
                color="primary"
                disabled={!dirty || !isValid}
                type="submit"
              >
                Зарегистрироваться
              </StyledButton>

              <LoginLinkSpan>
                <Link to="/blog-platform/login">Войти</Link>
              </LoginLinkSpan>
            </WrapperDiv>
          </Form>
        );
      }}
    </Formik>
  );
}

const WrapperDiv = styled.div`
  display: flex;
  flex-direction: column;
`;

const HeadingDiv = styled.div`
  margin-bottom: 20px;
  padding-top: 0;
`;
const H1 = styled.h1`
  margin: 0;
`;

const InputDiv = styled.div`
  margin-bottom: 20px;
`;

const LoginLinkSpan = styled.span`
  display: block;
  padding-top: 20px;
`;

